}

a {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.benefits__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.benefits__item {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 50px;
  padding-left: 5rem;
}

.benefits__item + .benefits__item {
  margin-top: 1rem;
}

.benefits__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 2%;
  width: 58px;
  height: 45px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.benefits__item--eff::before {
  background-image: url("../images/svg-sprites/sprite.svg#benefits-eff");
}

.benefits__item--dose::before {
  background-image: url("../images/svg-sprites/sprite.svg#benefits-dose");
}

.benefits__item--tab::before {
  background-image: url("../images/svg-sprites/sprite.svg#benefits-tab");
