  margin: 0;
  font-family: "Merriweather-Bold", "Arial", sans-serif;
  font-weight: 700;
  color: #242424;
  font-size: 0.875rem;
  line-height: 1.1428571429;
}

.benefits__tooltip-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  width: 20px;
  height: 19px;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  mask-image: url("../images/svg-sprites/sprite.svg#benefits-info");
  background-color: #47093E;
  transform: translateY(5px);
