  opacity: 0;
  position: absolute;
  top: 110%;
  left: 50%;
  transform: translateX(-50%);
  min-width: 200px;
  padding: 0.5rem;
  text-align: center;
  border: 1px solid #47093E;
  border-radius: 26px;
  transition: opacity 0.1s ease-in-out;
}

